// a library to wrap and simplify api calls
import AppConfig from '../../../Config/AppConfig'
import {getSession,generateHmac, generateSha256} from '../../../Utils/Utils'

export const create = api => ({
  changePassword: ({password,new_password}) => {
    const merchant_id=getSession('merchant_id')
    const body = `mutation{
      changePasswordMerchant(merchant_id:"${merchant_id}",password:"${password}",new_password:"${new_password}") {
          success
          status
          error
        }
      }`
    console.log("body change password====>",body)
    const query= { query:body }
    api.setHeader('hmac', generateHmac(JSON.stringify(query)))
    const resp = api.post('/graphql', query)
    return resp
  }
})
