// --- import list page entity ---
import _ from 'lodash'
// import { merge } from 'ramda'
import PageHome from '../Pages/Home/PageHome'
import LoginPageContainer from '../Pages/Login/PageLogin'
import SignupPageContainer from '../Containers/Signup/SignupPageContainer'
//New Rp
import PageHistoryTransaction from '../Pages/HistoryTransaction/PageHistoryTransaction'
import PageTopUpSaldoEwallet from '../Pages/TopupSaldoEwalletByCode/PageTopUpSaldoEwallet'
import ForgetPassword from '../Pages/ForgetPassword/ForgetPassword'
import MyAccount from '../Pages/MyAccount/PageMyAccount'
import MyProfile from '../Pages/MyProfile/PageMerchantMyprofile'
import Settlement from '../Pages/Settlement/PageSettlement'

const lp = {
  '/home': { route: '/home', path: '/home', component: PageHome },
  '/login': { route: '/login', path: '/login', component: LoginPageContainer},
  '/signup': { route: '/signup', path: '/signup', component: SignupPageContainer},
  '/history-transaction': { route: '/history-transaction', path: '/history-transaction/', component: PageHistoryTransaction },
  '/topup-ewallet-saldo': { route: '/topup-ewallet-saldo', path: '/topup-ewallet-saldo', component: PageTopUpSaldoEwallet},
  '/forget-password': { route: '/forget-password', path: '/forget-password', component: ForgetPassword},
  '/my-settlement': { route: '/my-settlement', path: '/my-settlement', component: Settlement},
  '/my-account': { route: '/my-account', path: '/my-account', component: MyAccount},
  '/my-profile': { route: '/my-profile', path: '/my-profile', component: MyProfile}
}
export const pageList = _.map(lp, (v) => v)
export const getPage = (pageId) => lp[pageId] || {}
